/* global area - start
==================================================================================================== */
body {
  margin: 0px;
  padding: 0px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.65;
  font-style: normal;
  font-display: swap;
  color: #6b6b6b;
  background-color: #ffffff;
  text-rendering: optimizelegibility;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-family: "Open Sans", sans-serif;
}

::selection {
  color: #ffffff;
  background-color: #ce0b2b;
}

::-moz-selection {
  color: #ffffff;
  background-color: #ce0b2b;
}

[data-background] {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

main {
  overflow: hidden;
  position: relative;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #000000;
  font-weight: 700;
  font-family: "Jost", sans-serif;
}

a,
button {
  cursor: pointer;
  display: inline-block;
  text-decoration: none;
}
a:hover,
button:hover {
  text-decoration: none;
}
a:focus,
button:focus {
  outline: none;
}

button {
  margin: 0px;
  padding: 0px;
  border: none;
  background: none;
}
button:focus {
  outline: none;
}

img {
  height: auto;
  max-width: 100%;
}

hr {
  height: 1px;
  border: none;
  display: block;
  margin: 30px 0px;
  background-color: #e8e8e8;
}

.form_item {
  position: relative;
  margin-bottom: 30px;
}
.form_item input,
.form_item textarea {
  width: 100%;
  border: none;
  outline: none;
  display: block;
  border: 1px solid #e8e8e8;
  transition: 0.6s cubic-bezier(0.25, 1, 0.5, 1);
}
.form_item input:focus,
.form_item textarea:focus {
  border-color: #ce0b2b;
}
.form_item input {
  height: 60px;
  padding: 0px 30px;
}
.form_item textarea {
  min-height: 130px;
  padding: 20px 30px;
}
.form_item .submit_btn {
  top: 50%;
  right: 30px;
  position: absolute;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

:-moz-placeholder {
  text-transform: capitalize;
}

::-moz-placeholder {
  text-transform: capitalize;
}

:-ms-input-placeholder {
  text-transform: capitalize;
}

::-webkit-input-placeholder {
  text-transform: capitalize;
}

.checkbox_item input {
  float: left;
  width: 25px;
  height: 25px;
  outline: none;
  cursor: pointer;
  -webkit-appearance: none;
          appearance: none;
  margin-right: 10px;
  position: relative;
  border: 2px solid #e6e6e6;
  transition: 0.6s cubic-bezier(0.25, 1, 0.5, 1);
}
.checkbox_item input:before {
  top: 50%;
  left: 50%;
  opacity: 0;
  color: #ffffff;
  font-size: 14px;
  content: "\f00c";
  font-weight: 400;
  position: absolute;
  transition: 0.6s cubic-bezier(0.25, 1, 0.5, 1);
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-family: "Font Awesome 5 Pro";
}
.checkbox_item input:checked {
  border-color: #ce0b2b;
  background-color: #ce0b2b;
}
.checkbox_item input:checked:before {
  opacity: 1;
}
.checkbox_item label {
  margin: 0px;
  cursor: pointer;
}

.option_select {
  position: relative;
  margin-bottom: 30px;
}
.option_select .nice-select {
  float: none;
  width: 100%;
  border: none;
  height: 60px;
  font-size: 16px;
  line-height: 60px;
  padding: 0px 30px;
  border-radius: 0px;
  background-color: #ffffff;
  transition: all 0.2s ease-in-out;
}
.option_select .nice-select:after {
  margin: 0px;
  width: auto;
  height: auto;
  border: none;
  float: right;
  content: "\f0d7";
  font-weight: 700;
  -webkit-transform: unset;
          transform: unset;
  position: static;
  margin-left: 10px;
  display: inline-block;
  -webkit-transform-origin: unset;
          transform-origin: unset;
  transition: 0.6s cubic-bezier(0.25, 1, 0.5, 1);
  font-family: "Font Awesome 5 Pro";
}
.option_select .nice-select .list {
  width: 100%;
  border: none;
  margin-top: 1px;
  min-width: 200px;
  max-height: 230px;
  overflow-y: scroll;
  border-radius: 0px;
  box-shadow: 0px 5px 20px 1px rgba(0, 0, 0, 0.15);
}
.option_select .nice-select .list::-webkit-scrollbar {
  width: 0px;
}
.option_select .nice-select .list .option {
  min-height: 45px;
  line-height: 45px;
  padding: 0px 30px;
  white-space: nowrap;
  color: #6b6b6b;
}

.quantity_input {
  height: 60px;
  padding: 0px 20px;
  text-align: center;
  align-items: center;
  border-radius: 45px;
  display: inline-flex;
  justify-content: center;
  border: 2px solid #eceef2;
}
.quantity_input input {
  width: 100px;
  border: none;
  padding: 0px;
  outline: none;
  font-size: 18px;
  font-weight: 600;
  text-align: center;
}
.quantity_input span {
  color: #ced9df;
  line-height: 1;
  cursor: pointer;
  font-size: 30px;
  margin: 0px 10px;
  transition: 0.6s cubic-bezier(0.25, 1, 0.5, 1);
}
.quantity_input span:hover {
  color: #ce0b2b;
}

