  max-width: 1630px;
}

.tab-content > .tab-pane {
  opacity: 0;
  height: 0px;
  display: block;
  visibility: hidden;
}

.tab-content > .active {
  opacity: 1;
  height: auto;
  visibility: visible;
}

.collapse_dropdown {
  top: 100%;
  right: 0px;
  margin-top: 5px;
  min-width: 230px;
  position: absolute;
  background-color: #ffffff;
  box-shadow: 0px 10px 33.25px 1.75px rgba(0, 27, 103, 0.15);
}
.collapse_dropdown .profile_info {
  padding: 20px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #f4f8fb;
}
.collapse_dropdown .profile_info .user_thumbnail {
  width: 40px;
  overflow: hidden;
  margin-right: 10px;
  border-radius: 100%;
}
.collapse_dropdown .profile_info .user_name {
  font-size: 15px;
  font-weight: 600;
  margin-bottom: 2px;
  color: #2e2e2e;
}
.collapse_dropdown .profile_info .user_title {
