.newCatePage {
  flex: 4;
  padding: 20px;

  form {
    .item {
      width: 100%;
      display: flex;
      flex-direction: column;
      margin-top: 10px;
      margin-right: 20px;

      label {
        margin-bottom: 10px;
        font-size: 14px;
        font-weight: 600;
        color: black;
      }

      input[type="text"] {
        width: 100%;
        height: 50px;
        padding: 10px;
        border: 1px solid rgb(189, 189, 189);
        border-radius: 5px;
        transition: 0.1s ease-in-out;

        &:active,
        &:focus {
          outline: 0;
          border: 2px solid rgb(0, 150, 187);
        }
      }
    }

    .activeContainer {
      select {
        width: 100%;
        height: 50px;
        border-radius: 5px;
        color: #555;
        padding-left: 8px;
      }
    }

    button {
      width: 200px;
      border: none;
      background-color: rgb(0, 149, 194);
      color: white;
      padding: 7px 10px;
      font-weight: 600;
      border-radius: 10px;
      margin-top: 30px;
      cursor: pointer;
      padding-left: 65px;
    }
  }
}

.edit {
  display: flex;
  width: 100%;
}

.edit-box {
  width: 50%;
}

.edit-box:first-child {
  margin-right: 20px;
}

.btn-createProduct {
  padding: 10px 21px;
  font-size: 18px;
}
